import React from 'react';
import {getLang} from "@web3game/lang";
import {Page} from "react-onsenui";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {withAPI} from "@web3game/common/components/withAPI";
import {createHeaderRender} from "../Header";


export function MyEarningPagePure() {
    const renderFixed = createHeaderRender(getLang().HOME_PAGE.MY_EARNING);

    return (
        <Page renderFixed={renderFixed}>
            <PagePadder />
            <div style={{ padding: 10 }}>
                My Earning
                <br />
            </div>
        </Page>
    );
}

export const MyEarningPage = compose(
    hot(module),
    React.memo,
    watchAccountChange,
    withAPI(async () => {

        return {};
    }),
)(MyEarningPagePure);

export default MyEarningPage;
