import React from 'react';
import {getLang} from "@web3game/lang";
import {Page, List, ListItem, ListTitle} from "react-onsenui";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {createHeaderRender} from "../Header";
import {Block} from "../common/Block";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {withAPI} from "@web3game/common/components/withAPI";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import { TradingLog } from '@web3game/typings/home';
import moment from 'moment';


type Events = {
    events: TradingLog[];
};

export type EventPageProps = Events & {

};

export function TradingLogPagePure({ events, }: EventPageProps,) {
    const renderFixed = createHeaderRender(getLang().HOME_PAGE.TRADING_LOG);
    return (
        <Page renderFixed={renderFixed}>
            <PagePadder />
            <Block noMargin oneHorizontalPadding transparent>
                <Block noMargin>
                    <div className="textGrey">
                        <ListTitle style={{ fontSize: 16 }}>
                            {getLang().HOME_PAGE.TRADING_LOG}
                        </ListTitle>
                        <List
                            style={{fontSize: "12px"}}
                            dataSource={events}
                            renderRow={(item: any, index: number) => (
                                <ListItem
                                    key={index}
                                >
                                    <span>{index + 1}.</span>
                                    <span style={{paddingLeft: "7px"}}>{item.from.slice(36)}</span>
                                    <span style={{paddingLeft: "7px"}}>{item.amount}</span>
                                    <span style={{paddingLeft: "7px"}}>{moment(new Date(item.time)).format('DD/MM HH:mm')}</span>
                                    <span style={{paddingLeft: "7px"}}>{item.eventName}</span>
                                </ListItem>

                            )}
                        />
                    </div>
                </Block>
            </Block>
        </Page>
    );
}

export const TradingLogPage = compose<EventPageProps>(
    hot(module),
    React.memo,
    watchAccountChange,
    withAPI(async () => {
        const homeAPI = await getHomeAPI();
        const events = await homeAPI.getTradingLog();
        return {
            events
        };
    }),
)(TradingLogPagePure);

export default TradingLogPage;
